a:hover,a:focus{
  text-decoration: none !important;
  outline: none !important;
}
.testimonial{
  text-align: center;
  color: #F2EFE8;
  margin:80px 30px;
  padding: 30px 40px;
  border-radius:30px;
  position: relative;
  z-index: 1;
}
.testimonial:before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: aliceblue;
  border-radius:30px;
  z-index: -1;
}
p.carousel-status {
  display: none;
}
.testimonial:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 101%;
  height: 106%;
  background-color: #3368b6;
  border-radius: 30px;
  transform: rotate(-0deg);
  z-index: -2;
}
