.testimonial .testimonial-content{
  font-size: 15px;
  letter-spacing: 1px;
  line-height: 30px;
  margin-bottom: 20px;
}
.testimonial .testimonial-title a{
  display: inline-block;
  color: #3368b6;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.testimonial .testimonial-title small{
  color: #F2EFE8;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: capitalize;
}
.owl-theme .owl-controls{
  margin-top: 0px;
  margin-left: 10px;
  text-align: center;
}
.owl-theme .owl-controls .owl-buttons div{
  display: inline-block;
  opacity:1;
  background:#FB8C00;
  color: #F2EFE8;
  border-radius: 0;
  width: 40px;
  height: 40px;
  line-height: 40px;
  margin-right: 5px;
}
.owl-prev:before,
.owl-next:before{
  content: "\f060";
  font-family: 'FontAwesome';
  font-size: 20px;
}
.owl-next:before{
  content: "\f061";
}